@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
}

:root{
    --color-bg:#460C68;
    --color-bg-variant:#7F167F;
    --color-primary:#913175;
    --color-primary-variant:#CD5888;
    --color-white:#fff;
    --color-light:#E9E8E8;

    --transition:all 400ms ease;
    --container-width-large: 75%;
    --container-width-medium: 86%;
    --container-width-small: 90%;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: "Poppins",sans-serif;
    background:var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

/* ---------------- styles ------------------------*/

.container {
    width: var(--container-width-large);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
}

section > h2, section> h5{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white)
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background:var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background:var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ------------------ media queries (for medium devices) ---------------------------------*/

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-medium);
    }
    section{
        margin-top: 6rem;
    }
}

/* ------------------ media queries (for small devices) ---------------------------------*/

@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-small);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}